class ImageUploader {
  async upload(file) {
    // file  
    const formData = new FormData();
    formData.append('file', file);
    formData.append('upload_preset', 'stvhoku6'); // upload preset 

    const result = await fetch(
      'https://api.cloudinary.com/v1_1/dnvzuljdl/image/upload', //   cloud name 
      {
        method: 'POST',
        body: formData,
      }
    );
    return await result.json();
  }
}

export default ImageUploader;
